import React, { lazy, Suspense, useContext, useEffect, useState } from "react";
import { ResourcesListData } from "../../../App";
import { Row, Col } from "../../common/RowColStyle";
const Card = lazy(() => import("./Card"));

function Cards(props) {
  const ResourcesListDataContext = useContext(ResourcesListData);
  const [data, setData] = useState([]);

  useEffect(() => {
    console.log("am i called?");
    if (ResourcesListDataContext.resourcesListState.data) {
      if (ResourcesListDataContext.resourcesListState.data.response) {
        setData(ResourcesListDataContext.resourcesListState.data.response);
      }
    }
  }, [ResourcesListDataContext]);

  return (
    <Row>
      {console.log("am i called? 1", data)}
      {data.map((d) => {
        return (<Col width="50%">
          {console.log("am i called? 2")}
          <Suspense fallback={<></>}>
            <Card mod={props.mod} data={d}/>
          </Suspense>
        </Col>)
      })}
    </Row>
  );
}

export default Cards;
